@import './components/index.css';

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
  scroll-behavior: smooth;
  color: rgb(65, 65, 65);
}

.home,
.services,
.products,
.sign-up {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

p {
  font-family: 'brandon';
  white-space: pre-line;
}

.section-text {
  margin-top: 20px;
  margin-bottom: 80px;
}

.section-title {
  font-family: 'didot';
  display: flex;
  font-weight: bolder;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 280px;
  padding: 32px 80px 40px;
  background-color: rgb(237, 218, 203);
  margin-bottom: 50px;
  margin-top: 20px;
}

.inner-section {
  margin-top: 25px;
  margin-bottom: 30px;
}

.section-sub-title {
  font-family: 'didot';
}

.divider {
  width: 100%;
  border-bottom-style: solid;
  border-bottom-color: #d3b884;
  border-bottom-width: 1px;
  margin-bottom: 32px;
}

.products {
  background-image: url('/images/img-1.jpg');
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

.sign-up {
  background-image: url('/images/img-8.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;
  font-size: 100px;
}

@font-face {
  font-family: 'serengetti_itcregular';
  src: url('/serengetti_itc_regular-webfont.woff2') format('woff2'),
    url('/serengetti_itc_regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

